import logo from './logo.svg';
import './App.css';
import React ,{ useState } from 'react';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import axios from 'axios';


function App() {

  const [temp, setTemp] = useState(0);
  const [max, setmax] = useState(0);
  const [stringToStart, setStringToStart] = useState("");
  const [rap, setRap] = useState("");
  function submit(){
    console.log(stringToStart)
    console.log(max)
    console.log(temp)
    axios.get(`http://127.0.0.1:5000/generate`,{params:{
      string_to_start:stringToStart,
      max_length:max,
      temperature:temp
    }})
      .then(res => {
        console.log(res);
        setRap(res.data.rap)
      })
  }
  return (
    <div className="App">
      <header className="App-header">
      <TextField
          id="standard-number"
          label="max length"
          type="number"
          InputLabelProps={{
            shrink: true,
          }}
          onChange={(e) =>setmax(e.currentTarget.value)}
        />
        <TextField id="filled-search" label="temp" type="search" variant="filled" onChange={(e) =>setTemp(e.currentTarget.value)}/>
        <TextField id="filled-search" label="שורה להתחלה" type="search" variant="filled" onChange={(e) =>setStringToStart(e.currentTarget.value)}/>
        <Button variant="contained" onClick={()=>submit()}>Submit</Button>
        <h6>{rap}</h6>
      </header>
    </div>
  );
}

export default App;
